/**
 * React hook for managing WebSocket connections with input methods.
 */

import { useEffect, useRef, useState } from 'react'
import { AccessibilityWebSocket, createWebSocketClient, generateClientId } from './websocket-client'

type InputMethod = 'voice' | 'draw' | 'haptic' | 'sign' | 'eye-gaze' | 'head' | 'facial' | 'sip-puff' | 'type'

interface UseWebSocketOptions {
  inputMethod: InputMethod
  onExecutionResult?: (result: any) => void
  onCalibrationSaved?: () => void
  onGestureSaved?: () => void
  autoConnect?: boolean
}

export function useWebSocket({
  inputMethod,
  onExecutionResult,
  onCalibrationSaved,
  onGestureSaved,
  autoConnect = true
}: UseWebSocketOptions) {
  const [isConnected, setIsConnected] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const wsRef = useRef<AccessibilityWebSocket | null>(null)
  const clientIdRef = useRef<string>(generateClientId())

  useEffect(() => {
    if (!autoConnect) return

    const ws = createWebSocketClient({
      inputMethod,
      clientId: clientIdRef.current,
      onConnected: () => {
        setIsConnected(true)
        setError(null)
        console.log(`Connected to ${inputMethod} WebSocket`)
      },
      onDisconnected: () => {
        setIsConnected(false)
        console.log(`Disconnected from ${inputMethod} WebSocket`)
      },
      onError: (err) => {
        setError('Connection error')
        console.error('WebSocket error:', err)
      },
      onMessage: (message) => {
        switch (message.type) {
          case 'execution_result':
            onExecutionResult?.(message)
            break
          case 'calibration_saved':
            onCalibrationSaved?.()
            break
          case 'gesture_saved':
            onGestureSaved?.()
            break
        }
      }
    })

    wsRef.current = ws
    ws.connect()

    return () => {
      ws.disconnect()
    }
  }, [inputMethod, autoConnect, onExecutionResult, onCalibrationSaved, onGestureSaved])

  const executeQuery = (query: string) => {
    if (wsRef.current) {
      console.log(`Executing query via ${inputMethod}:`, query)
      wsRef.current.executeQuery(query)
    } else {
      console.warn('WebSocket not initialized')
    }
  }

  const saveCalibration = (profileId: string, data: any) => {
    if (wsRef.current) {
      wsRef.current.saveCalibration(profileId, data)
    }
  }

  const saveGesture = (profileId: string, gestureId: string, data: any) => {
    if (wsRef.current) {
      wsRef.current.saveGesture(profileId, gestureId, data)
    }
  }

  return {
    isConnected,
    error,
    executeQuery,
    saveCalibration,
    saveGesture
  }
}
