/**
 * Unified WebSocket client for accessibility input methods.
 */

type InputMethod = 'voice' | 'draw' | 'haptic' | 'sign' | 'eye-gaze' | 'head' | 'facial' | 'sip-puff'

type MessageType = 
  | 'connected'
  | 'ping'
  | 'pong'
  | 'execute_query'
  | 'execution_result'
  | 'save_calibration'
  | 'calibration_saved'
  | 'save_gesture'
  | 'gesture_saved'
  | 'echo'

interface WebSocketMessage {
  type: MessageType
  [key: string]: any
}

interface WebSocketConfig {
  inputMethod: InputMethod
  clientId: string
  onMessage?: (message: WebSocketMessage) => void
  onConnected?: () => void
  onDisconnected?: () => void
  onError?: (error: Event) => void
  autoReconnect?: boolean
  reconnectInterval?: number
  maxReconnectAttempts?: number
}

export class AccessibilityWebSocket {
  private ws: WebSocket | null = null
  private config: WebSocketConfig
  private reconnectAttempts = 0
  private reconnectTimer: NodeJS.Timeout | null = null
  private messageQueue: WebSocketMessage[] = []
  private isConnecting = false
  private heartbeatInterval: NodeJS.Timeout | null = null

  constructor(config: WebSocketConfig) {
    this.config = {
      autoReconnect: true,
      reconnectInterval: 3000,
      maxReconnectAttempts: 10,
      ...config
    }
  }

  /**
   * Connect to the WebSocket server.
   */
  connect(): void {
    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      console.warn('WebSocket already connected')
      return
    }

    if (this.isConnecting) {
      console.warn('Connection already in progress')
      return
    }

    this.isConnecting = true
    const url = `ws://127.0.0.1:8000/ws/${this.config.inputMethod}/${this.config.clientId}`

    console.log(`Connecting to ${url}`)

    try {
      this.ws = new WebSocket(url)

      this.ws.onopen = () => {
        console.log(`WebSocket connected for ${this.config.inputMethod}`)
        this.isConnecting = false
        this.reconnectAttempts = 0
        this.config.onConnected?.()
        this.flushMessageQueue()
        this.startHeartbeat()
      }

      this.ws.onmessage = (event) => {
        try {
          const message = JSON.parse(event.data) as WebSocketMessage
          this.handleMessage(message)
        } catch (error) {
          console.error('Failed to parse WebSocket message:', error)
        }
      }

      this.ws.onerror = (error) => {
        console.error('WebSocket error:', error)
        this.config.onError?.(error)
      }

      this.ws.onclose = () => {
        console.log('WebSocket closed')
        this.isConnecting = false
        this.stopHeartbeat()
        this.config.onDisconnected?.()
        this.attemptReconnect()
      }
    } catch (error) {
      console.error('Failed to create WebSocket:', error)
      this.isConnecting = false
      this.attemptReconnect()
    }
  }

  /**
   * Disconnect from the WebSocket server.
   */
  disconnect(): void {
    this.config.autoReconnect = false
    this.stopHeartbeat()
    this.clearReconnectTimer()

    if (this.ws) {
      this.ws.close()
      this.ws = null
    }
  }

  /**
   * Send a message to the server.
   */
  send(message: WebSocketMessage): void {
    if (!this.ws || this.ws.readyState !== WebSocket.OPEN) {
      console.warn('WebSocket not connected, queueing message')
      this.messageQueue.push(message)
      return
    }

    try {
      this.ws.send(JSON.stringify(message))
    } catch (error) {
      console.error('Failed to send message:', error)
      this.messageQueue.push(message)
    }
  }

  /**
   * Execute a natural language query.
   */
  executeQuery(query: string): void {
    this.send({
      type: 'execute_query',
      query,
      timestamp: new Date().toISOString()
    })
  }

  /**
   * Save calibration data.
   */
  saveCalibration(profileId: string, data: any): void {
    this.send({
      type: 'save_calibration',
      profile_id: profileId,
      data,
      timestamp: new Date().toISOString()
    })
  }

  /**
   * Save a custom gesture.
   */
  saveGesture(profileId: string, gestureId: string, data: any): void {
    this.send({
      type: 'save_gesture',
      profile_id: profileId,
      gesture_id: gestureId,
      data,
      timestamp: new Date().toISOString()
    })
  }

  /**
   * Check if connected.
   */
  isConnected(): boolean {
    return this.ws !== null && this.ws.readyState === WebSocket.OPEN
  }

  /**
   * Handle incoming messages.
   */
  private handleMessage(message: WebSocketMessage): void {
    // Handle pong responses
    if (message.type === 'pong') {
      return // Heartbeat acknowledged
    }

    // Call user's message handler
    this.config.onMessage?.(message)
  }

  /**
   * Flush queued messages when connected.
   */
  private flushMessageQueue(): void {
    while (this.messageQueue.length > 0 && this.isConnected()) {
      const message = this.messageQueue.shift()
      if (message) {
        this.send(message)
      }
    }
  }

  /**
   * Attempt to reconnect with exponential backoff.
   */
  private attemptReconnect(): void {
    if (!this.config.autoReconnect) {
      return
    }

    if (this.reconnectAttempts >= (this.config.maxReconnectAttempts || 10)) {
      console.error('Max reconnect attempts reached')
      return
    }

    this.clearReconnectTimer()

    const delay = Math.min(
      (this.config.reconnectInterval || 3000) * Math.pow(2, this.reconnectAttempts),
      30000 // Max 30 seconds
    )

    console.log(`Reconnecting in ${delay}ms (attempt ${this.reconnectAttempts + 1})`)

    this.reconnectTimer = setTimeout(() => {
      this.reconnectAttempts++
      this.connect()
    }, delay)
  }

  /**
   * Clear reconnect timer.
   */
  private clearReconnectTimer(): void {
    if (this.reconnectTimer) {
      clearTimeout(this.reconnectTimer)
      this.reconnectTimer = null
    }
  }

  /**
   * Start heartbeat to keep connection alive.
   */
  private startHeartbeat(): void {
    this.stopHeartbeat()
    this.heartbeatInterval = setInterval(() => {
      if (this.isConnected()) {
        this.send({ type: 'ping' })
      }
    }, 30000) // Ping every 30 seconds
  }

  /**
   * Stop heartbeat.
   */
  private stopHeartbeat(): void {
    if (this.heartbeatInterval) {
      clearInterval(this.heartbeatInterval)
      this.heartbeatInterval = null
    }
  }
}

/**
 * Create a WebSocket client for a specific input method.
 */
export function createWebSocketClient(config: WebSocketConfig): AccessibilityWebSocket {
  return new AccessibilityWebSocket(config)
}

/**
 * Generate a unique client ID.
 */
export function generateClientId(): string {
  return `client_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`
}
